.App {
    width: 90%;
    max-width: 1510px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.header {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
}

.filtered-container {
    width: 90%;
    max-width: 1510px;
    margin: 0 auto;
    position: absolute; 
    top: 120px;
    z-index: 999;
    display: flex;
}

.city-container { width: 40%;}

.edit-text { 
    z-index: 999; 
    font-weight: 700;
    position: absolute;
}


@media only screen and (max-width: 770px) {
    .header { flex-direction: column; } 
}
